.tableContainer{
    width: 95vw;
    margin: auto;
}

.table{
    /* width: 100%; */
    padding: 0.5rem;
    border-radius: 10px;
    background-color: white;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.tableHead{
    border-bottom: 1px solid;
    padding: 8px 0px 8px 0px;
}

.tableData{
    border-top: 1px solid;
    padding: 3px 0px 3px 0px;
}

.headContent{
    font-weight: 600;
}

.tableHead, .tableData{
    display: flex;
    justify-content: space-around;
}

.commonCollumn{
    width: 20%;
    text-align: center;
}

.blcAmtLable{
    width: 80%;
}

.blcAmtContent{
    width: 20%;
}

.blcAmtContainer{
    display: flex;
    margin-top: 10px;
    border-top: 2px solid;
    padding: 10px 0px;
}

@media screen {
    .printABC{
        display: none;
    }   
}

@media print {
    .printABC{
        display: block;
    }   
}