.spListContainer {
    width: 80%;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    align-self: center;
    margin: 5px auto 0px auto;
    padding: 7px;
    border-radius: 10px;
    background-color: white;
}

.spListTableHeading {
    padding: 10px;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2), 0 3px 10px 0 rgba(0, 0, 0, 0.19);
    border-radius: 5px;
}

.spListTableData {
    padding: 10px;
    box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.2), 0 1.5px 5px 0 rgba(0, 0, 0, 0.19);
}

.spListTableData,
.spListTableHeading {
    display: flex;
}

.spListSnHead {
    width: 10%;
    padding: 5px;
}

.spListNameHead {
    width: 30%;
    padding: 5px;
}

.spListLinkHead {
    width: 40%;
    padding: 5px;
}

.spListActionHead {
    width: 20%;
    padding: 5px;
}

.spListSnHead,
.spListNameHead,
.spListLinkHead {
    /* border: 0.5px solid red; */
    /* margin: 0px 3px; */
    /* box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.2), 0 1.5px 5px 0 rgba(0, 0, 0, 0.19); */
}

.spListTH {
    font-size: 17px;
    font-weight: 600;
}

.spIconsBtn {
    margin: 2px;
}


@media only screen and (max-width: 750px) {
    .spListContainer {
        width: 90%;
    }

    .spListSnHead {
        width: 20%;
    }

    .spListNameHead {
        width: 60%;
    }

    .spListLinkHead {
        display: none;
    }

    .spListActionHead {
        width: 20%;
    }
}