.flexCardContainer{
    width: 90%;
    margin: auto;
}

.rowContainer{
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    width: 90%;
    margin: auto;
    justify-content: center;
    /* padding: 0px 50px 0px 50px; */
    border-radius: 10px;
    background-image: repeating-linear-gradient(-26deg, rgba(255,255,255, 0.25), rgba(255,255,255, 0.25) 2px, transparent 3px, transparent 7px);
    background-size: 6px 8px;
    border: 2px solid;
}

.btnColContainer {
    position: relative;
    width: 200px;
    height: 200px;
    /* border: 1px solid; */
    top: 0;
    left: 0;
    background-color: black;
    margin: 15px 35px 15px 35px;
    /* box-shadow: 5px 5px 5px 5px; */
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    border-radius: 10px;
}

.btnImg {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0.5;
    border-radius: 10px;
}

.btnTitle {
    position: absolute;
    top: 40%;
    width: 100%;
    text-align: center;
    font-size: 18px;
    /* color: #99ccff; */
    color: white;
    text-shadow: 2px 2px 5px black;
}



