.spFormMainContainer {
    width: 80%;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    align-self: center;
    margin: 30px auto;
    padding: 10px;
    border-radius: 10px;
    background-color: white;
}

.spFormContainer {
    /* display: flex; */
    justify-content: center;
}

.frRowContainer{
    width: fit-content;
    margin: auto;
    display: flex;
    justify-content: center;
}

.spFormItem {
    /* margin-top: 10px; */
    margin: 10px 5px 0px 5px;
}

/* .frFormBtn{
    margin
} */

@media only screen and (max-width: 750px) {
    .spFormMainContainer {
        width: 90%;
    }

    .spFormItem {
        margin-top: 10px;
    }
}